/* Animated Text */

.rolltext h1 {
	text-align: left;
	color: #f1faee;
	font-size: 176px;
	line-height: 196px; /* 146 actual value */
	letter-spacing: -0.02em;
}

.roller {
	height: 196px;
	position: relative;
	overflow: hidden;
	width: 100%;
	display: flex;

	color: #1d3557;
}

.roller .rolltext {
	position: absolute;
	top: 0;
	animation: slide 7s infinite;
}

@keyframes slide {
	0% {
		top: 0;
	}

	35% {
		top: 0;
	}

	50% {
		top: -196px;
	}
	85% {
		top: -196px;
	}
	100% {
		top: 0px;
	}
}

@media screen and (max-width: 600px) {
	.rolltext h1 {
		text-align: left;
		color: #f1faee;
		font-size: 90px;
		line-height: 124px; /* 146 actual value */
		letter-spacing: -0.02em;
	}

	.roller {
		height: 116px;
		line-height: 124px;
	}

	.roller .rolltext {
		animation: slide-mob 5s infinite;
	}

	@keyframes slide-mob {
		0% {
			top: 0;
		}

		35% {
			top: 0;
		}

		50% {
			top: -124px;
		}
		85% {
			top: -124px;
		}
		100% {
			top: 0px;
		}
	}
}
